@import url('https://fonts.googleapis.com/css2?family=IM+Fell+DW+Pica&display=swap');

body, html {
  width:100%;
  height:100%;
  overflow:hidden;
  font-family:'IM Fell DW Pica', serif;
  background: #000;
}

#three-container {
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  z-index:0;
}

#app {
  position:relative;
  z-index:1;
  display:flex;
  height:100vh;
  color:#e4d5b7;
}

/* Clock left side */
#clock-wrap {
  flex:1;
  perspective:800px;
  display:flex;
  align-items:center;
  justify-content:center;
}

.layer {
  position:absolute;
  top:50%;
  left:50%;
  width:60vmin;
  height:60vmin;
  margin:-30vmin 0 0 -30vmin;
  background-size:cover;
  background-position:center;
  transform-style:preserve-3d;
  transition:transform 0.1s ease-out;
  pointer-events:none;
  animation: breathe 5s ease-in-out infinite;
}

#layer-back    { z-index: 1; }
#layer-gears   { background-size: contain; background-repeat: no-repeat; z-index: 2; }
#layer-astrol  { z-index: 3; }
#layer-hands   { z-index:5; }

.hand {
  position:absolute;
  transform-origin:50% 100%;
  top:50%;
  left:50%;
  background:#e4d5b7;
  border-radius:2px;
  transition:transform 0.05s linear;
}

#hour   { width:1vmin;  height:20vmin; margin:-20vmin 0 0 -0.5vmin; }
#minute { width:0.8vmin;height:26vmin; margin:-26vmin 0 0 -0.4vmin; }
#second { width:0.3vmin;height:30vmin; margin:-30vmin 0 0 -0.15vmin; background:#d28e2b; }

#pivot {
  position:absolute;
  width:2vmin;
  height:2vmin;
  background:#e4d5b7;
  border-radius:50%;
  top:50%;
  left:50%;
  margin:-1vmin 0 0 -1vmin;
  box-shadow:0 0 1vmin rgba(0,0,0,0.7);
}

/* Timetable right side */
#timetable {
  width:30vmin;
  max-width:350px;
  padding:2vmin;
  background:transparent;
  overflow-y:auto;
  font-size:1.5vmin;
}

#timetable h2 {
  font-size:2.5vmin;
  text-align:center;
  margin-bottom:1vmin;
  border-bottom:0.2vmin solid #d28e2b;
  padding-bottom:1vmin;
}

.event {
  display:flex;
  align-items:center;
  margin:1vmin 0;
  padding:0.5vmin 1vmin;
  border-left:0.5vmin solid #d28e2b;
  transition:background 0.3s, transform 0.2s;
}

.event.current {
  background:rgba(210,142,43,0.4);
}

.event:hover {
  background:rgba(255,255,255,0.1);
  transform:translateX(1vmin);
}

.time {
  width:7vmin;
  font-weight:bold;
}

/* Responsive */
@media(max-width:768px){
  #app {
    flex-direction:column;
  }
  #timetable {
    width:100%;
    font-size:3vw;
  }
  .layer {
    width:80vmin;
    height:80vmin;
    margin:-40vmin 0 0 -40vmin;
  }
}

@keyframes breathe {
  0% { transform: scale(1); }
  50% { transform: scale(1.02); }
  100% { transform: scale(1); }
}
